.c-contacto__inputs input, .c-contacto__inputs textarea {
  width: 100%;
  background-color: #f0f0f0;
  padding: 0.625rem;
  border: 2px solid rgba(255, 193, 0, 0.8784313725);
  border-radius: 0.625rem;
  margin: 0.625rem 0;
}
.c-contacto__inputs textarea {
  min-height: 7.5rem;
  max-height: 18.75rem;
  min-width: 100%;
  max-width: 100%;
}
.c-contacto__btn {
  margin: 20px 0;
  color: #181818;
}
.c-contacto__respuesta {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;
}

.c-footer {
  background-color: #181818;
  padding: 0.5rem;
  margin-bottom: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 480px) {
  .c-footer {
    padding: 1.25rem;
  }
}
@media (min-width: 768px) {
  .c-footer {
    width: calc(100% - 2.5rem);
    margin-bottom: 1.25rem;
  }
}
.c-footer__autor {
  color: #f0f0f0;
  font-size: 1em;
}
@media (min-width: 768px) {
  .c-footer__autor {
    font-size: 1.25rem;
  }
}
.c-footer__autor span {
  color: #f0f0f0;
  font-size: 0.9375rem;
}
.c-footer__sociales {
  color: #f0f0f0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.c-footer__sociales a {
  padding: 0.625rem;
  font-size: 1.25em;
}
.c-footer__flecha i {
  color: #f0f0f0;
}
@media (min-width: 768px) {
  .c-footer__flecha {
    display: none;
  }
}

.u-rotation {
  transform: rotate(180deg);
}

.u-display--flex {
  display: flex;
}
.u-display--none {
  display: none;
}